ul {
  padding: 0;
  margin: 0;
}
.programs__section {
  padding-top: 50px;
  padding-bottom: 537px;
  background-color: $alt-background-color;
}
.programs__container {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.programs__title {
  font-weight: 800;
  font-size: 20px;
  line-height: 1.3;
  text-align: center;
  letter-spacing: 0.03em;
  color: $alt-text-color;
  margin-bottom: 43px;
}
.programs__periods::before {
  content: '';
  display: none;
  vertical-align: middle;
  width: 2px;
  height: 1190px;
  background-color: $alt-text-color;
  position: absolute;
  top: 8px;
  left: 0;
}
.programs__periods {
  position: relative;
  align-self: center;
 

  &::after {
    content: '';
    position: absolute;
    top: 1180px;
    left: 122px;
    display: block;
    width: 120px;
    height: 137px;
    background-image: url(../images/programs/programs-mobile@1x.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    opacity: 0.5;
    border: 1px solid #000000;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}
}
.programs__period {
  padding-left: 26px;
}

// 0-767@2x
@media (min-devise-pixel-ratio: 2) and (max-width: 767px),
  (min-resolution: 192dpi) and (max-width: 767px),
  (min-resolution: 2dppx) and (max-width: 767px) {
  .programs__periods::after {
    background-image: url(../images/programs/programs-mobile@2x.jpg);
  }
}

.programs__period:not(:last-child) {
  border-left: 1px solid $alt-text-color;
}

.programs__period-title {
  display: block;
  margin-bottom: 0px;
  font-weight: 700;
  font-size: 14px;
  line-height: 1;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: $alt-text-color;
}

.programs__period.first::before {
  content: '';
  position: absolute;
  top: 0px;
  left: -6px;
  display: inline-block;
  vertical-align: middle;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: $accent-background-color;
}
.programs__period.second {
  padding-top: 40px;
  &::before {
    content: '';
    position: absolute;
    top: 206px;
    left: -6px;
    display: inline-block;
    vertical-align: middle;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: $accent-background-color;
  }
}
.programs__period.third {
  padding-top: 40px;
  &::before {
    content: '';
    position: absolute;
    top: 430px;
    left: -6px;
    display: inline-block;
    vertical-align: middle;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: $accent-background-color;
  }
}
.programs__period.fourth {
  padding-top: 40px;
  padding-bottom: 40px;
  &::before {
    content: '';
    position: absolute;
    top: 748px;
    left: -6px;
    display: inline-block;
    vertical-align: middle;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: $accent-background-color;
  }
}
.programs__period.fifth {
  &::before {
    content: '';
    position: absolute;
    top: 1127px;
    left: -6px;
    display: inline-block;
    vertical-align: middle;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: $accent-background-color;
}
}

.programs__practice-block-title{
    display: block;
    margin-top: 8px;
    margin-bottom: 8px;
    font-weight: 700;
    font-size: 12px;
    line-height: 1.64;
    letter-spacing: 0.08em;
    color: $accent-background-color;


}
.hidden{
    visibility: hidden;
}
.programs__practice-block-title::before{
    content: "";
    display: inline-block;
    vertical-align: middle;
    width: 2px;
    height: 15px;
    background-color: $accent-background-color;
    transform: rotate(90deg);
    margin-right: 10px;
    margin-left: 7px;
    visibility: visible;
    
}
.programs__practice-topic-item{
    display: block;
    width: 176px;
    
    font-weight: 500;
    font-size: 12px;
    line-height: 1.67;
    letter-spacing: 0.08em;
    color: $light-gray-color;
    opacity: 0.5;
}



