@media screen and (min-width: $desktop) {
  .programs__section {
    padding-top: 120px;
    padding-bottom: 363px;
  }
  
  .programs__container {
    display: block;
    position: relative;
    
  }
  
  .programs__title {
    margin-bottom: 100px;
    text-align: start;
    font-size: 46px;
  }

  .programs__periods {
    display: flex;
    align-items: flex-start;
    position: relative;
    padding-left: 0px;
    padding-top: 60px;
    border-top: 1px solid $alt-text-color;
    &::before {
      display: none;
      height: 700px;
      top: 0;
      left: 0;
      transform: rotate(90deg);
    }
    &::after {
      content: '';
      position: absolute;
      left: -230px;
      top: 460px;
      width: 760px;
      height: 502px;
      opacity: 0.5;
      background-image: url(../images/programs/programs-desktop@1x.jpg);
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
    }
  }
  .programs__period.first {
    border-left: none;
    padding-left: 0px;
    padding-top: 0px;
    &::before {
      top: -6px;
      left: 0px;
    }
  }
  .programs__period.second {
    border-left: none;
    padding-left: 0px;
    padding-top: 0px;
    margin-top: 30px;
    &::before {
      top: -6px;
      left: 270px;
    }
  }

  .programs__period.third {
    padding-left: 0px;
    padding-top: 0px;
    border-left: none;
    margin-top: 30px;
    &::before {
      top: -6px;
      left: 540px;
    }
  }
  .programs__period.fourth {
    padding-left: 0px;
    padding-top: 0px;
    border-left: none;
    margin-top: 30px;
    &::before {
      top: -6px;
      left: 810px;
    }
  }
  .programs__period.fifth {
    padding-left: 0px;
    padding-top: 0px;
    margin-top: 30px;
    &::before {
      top: -6px;
      left: 1080px;
    }
  }

  .programs__period.second {
    margin-top: 0;
    margin-left: 30px;
  }
  .programs__period.third {
    margin-top: 0;
    margin-left: 30px;
  }
  .programs__period.fourth {
    margin-top: 0;
    margin-left: 30px;
  }
  .programs__period.fifth {
    margin-top: 0;
    margin-left: 30px;
  }
  .programs__period-title {
    font-size: 20px;
  }
  .programs__practice-block {
    width: 240px;
  }

  .programs__practice-block {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  .programs__practice-block-title {
    font-size: 20px;
    margin-top: 25px;
    margin-bottom: 10px;
  }

  .programs__practice-block-item {
    &:not(:first-child) {
      margin-top: 0;
      margin-left: 0px;
    }
  }
  .programs__practice-topic-item {
    font-size: 18px;
    line-height: 1.55;
  }
}

//2x 1366px+
@media (min-devise-pixel-ratio: 2) and (min-width: $desktop),
  (min-resolution: 192dpi) and (min-width: $desktop),
  (min-resolution: 2dppx) and (min-width: $desktop) {
  .programs__periods::after {
